.contact-form {
  width: 350px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.contact-form label {
  margin-bottom: 5px;
  font-weight: bold;
  display: block;
}

.contact-form input[type="text"], .contact-form input[type="tel"], .contact-form input[type="email"], .contact-form textarea {
  width: 328px;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin-bottom: 15px;
  padding: 10px;
}

.contact-form button {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 3px;
  padding: 10px;
  display: block;
}

.contact-form button:hover {
  background-color: #0056b3;
}

ul {
  padding: 0;
  list-style: none;
}

.card {
  width: 400px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.card-content {
  text-align: left;
}

.card-name {
  margin: 0;
  font-size: 20px;
  font-weight: bold;
}

.card-phone {
  margin: 5px 0;
  font-size: 16px;
}

.card-email {
  color: #007bff;
  margin: 5px 0;
  font-size: 16px;
}

.card-comment {
  color: #666;
  margin: 10px 0;
  font-size: 14px;
}

